.login{
    background: #F8F9FA;
    width: 40%;
    margin: 7% auto 0;
    padding: 40px 50px; 
    box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.2);
    
}
.login h2{
    font-style: normal;
    font-weight: 600;
    font-size: 40px;
    color: #2D3748;
    text-align: center;
    margin-bottom: 50px;
}
.login form label{
    font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 30px;
color: #2D3748;
margin-bottom: 6px;
}
.login input{
    border: none;
    outline: none;
    background: #FFFFFF;
box-shadow: 0px 3.5px 5.5px rgba(0, 0, 0, 0.02);
border-radius: 24px;
padding-top: 10px;
padding-bottom: 10px;
box-shadow: 0px 3.5px 5.5px rgba(0, 0, 0, 0.05);
}
.login form button{
    border: none;
    outline: none;
    width: 100%;
    background: #4FD1C5;
box-shadow: 0px 3.5px 5.5px rgba(0, 0, 0, 0.02);
border-radius: 24px;
text-align:center;
font-style: normal;
font-weight: 600;
font-size: 24px;
line-height: 36px;
color: #FFFFFF;
padding: 10px 0;
}
.login .form-group{
    margin-bottom: 30px;
}