}

.blog .items img {
    width: 140px;
    height: 140px;
    margin-right: 20px;
}

.blog .items .title {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: black;
    margin: 10px 0 10px;
}

.blog .items .lorem p {
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 18px;
}
.blog .blog_items .top p{
    font-style: normal;
font-weight: 500;
font-size: 17px;
line-height: 24px;
color: #000000;
}
